import React, {useState} from 'react';

import logoAppOne from '../interface/img/png/shifrator.png'

import backImg from '../interface/img/svg/backSHIFR.svg'
import emptyImg from '../interface/img/svg/voprosSHIFR.svg'

import ShifratorApp from './appForAppStore/ShifratorApp';


const AppStore = () => {

    const [IsShifratorApp, setIsShifratorApp] = useState(false)
    const vopros_1 = 
    `
         :     
    .       
     -  .         .
            ,  .
     :  ;      -   :)
    `

    const activate = () => {
        const block1 = document.body.querySelector('.mb-r1')
        block1.classList.add('goOut-1')
        const block2 = document.body.querySelector('.mb-r2')
        block2.classList.add('goOut-2')
        const block3 = document.body.querySelector('.mb-r3')
        block3.classList.add('goOut-3')
        const block4 = document.body.querySelector('.mb-r4')
        block4.classList.add('goOut-4')
        const blockMain = document.body.querySelector('.bigBlock_appStore')
        blockMain.classList.add('border-none')

        setTimeout( () => {
            setIsShifratorApp(true)
            block1.classList.add('none')
            block2.classList.add('none')
            block3.classList.add('none')
            block4.classList.add('none')
        }, 1000)
        
    }

    const closeApps = () => {
        setIsShifratorApp(false)

        const block1 = document.body.querySelector('.mb-r1')
        const block2 = document.body.querySelector('.mb-r2')
        const block3 = document.body.querySelector('.mb-r3')
        const block4 = document.body.querySelector('.mb-r4')

        block1.classList.remove('none')
        block2.classList.remove('none')
        block3.classList.remove('none')
        block4.classList.remove('none')

        block1.classList.remove('goOut-1')
        block2.classList.remove('goOut-2')
        block3.classList.remove('goOut-3')
        block4.classList.remove('goOut-4')

        const blockMain = document.body.querySelector('.bigBlock_appStore')
        blockMain.classList.remove('border-none')
    }

    const viewStore = () => {
        const block = document.body.querySelector('.appStore')

        const coords = block.getBoundingClientRect()

        if(coords.top < window.innerHeight - 200){
            block.classList.add('opacityShow')
        }
    }

    window.addEventListener('scroll', viewStore)

    return (
        <section className='appStore'>
            <div className='container'>
                <div className='appStore_block'>
                    <div className='title-medium'>
                        ./2  -
                    </div>
                    <div className='subtitle-medium'>
                             ,  
                          .     
                        - ,    .
                        , ,  .
                          .
                    </div>

                    <div className='bigBlock_appStore'>

                        {IsShifratorApp && 
                            <div className='prilojenie_appStore'>
                                <div className='back_appStore'>
                                    <img src={backImg} className='backImg_appStore' onClick={() => closeApps()}/>
                                    <span data-text={vopros_1}>
                                        <img src={emptyImg} className='voprosImg_appStore' />
                                    </span>
                                </div>  
                                <ShifratorApp />
                            </div>
                        }

                        <div className='miniBlock_appStore mb-r1' onClick={() => activate()}>
                            <div className='subtitle-premedium fontw500'></div>
                            <img src={logoAppOne} className='logo_miniBlock_appStore' />
                        </div>
                        <div className='miniBlock_appStore mb-r2'>
                            <div className='subtitle-premedium fontw500'></div>
                            <img src={emptyImg} className='logo_miniBlock_appStore' />
                        </div>
                        <div className='miniBlock_appStore mb-r3'>
                            <div className='subtitle-premedium fontw500'></div>
                            <img src={emptyImg} className='logo_miniBlock_appStore' />
                        </div>
                        <div className='miniBlock_appStore mb-r4'>
                            <div className='subtitle-premedium fontw500'></div>
                            <img src={emptyImg} className='logo_miniBlock_appStore' />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default AppStore;