import React, {useState} from 'react';

import {createCode, decodedCode} from '../../app/shifrator'
import spinner from '../../interface/img/spinner.gif'


const ShifratorApp = () => {

    const [isShifr, setIsShifr] = useState(true)

    const [hard, setHard] = useState(2)
    const [message, setMessage] = useState('')
    const [oneKey, setOneKey] = useState('')
    const [twoKey, setTwoKey] = useState('')

    const [deOneKey, setDeOneKey] = useState('')
    const [deTwoKey, setDeTwoKey] = useState('')

    const [deMessage, setDeMessage] = useState('')

    const loading = () => {
        const blocks = document.getElementById('exitCode-1')
        blocks.style.display = 'none'

        const blockLoad = document.body.querySelector('.loadingShifr')
        blockLoad.classList.remove('none')

        setTimeout(() => {
            blocks.style.display = 'block'

            blockLoad.classList.add('none')
        }, 1000)

    }

    const createShifr = () => {
        if (message){
            const [key1, key2] = createCode(message, Number(hard))

            loading()

            setOneKey(key1)
            setTwoKey(key2)
        } else{
            const block = document.getElementById('id_message')
            block.classList.add('inputWarning')

            setTimeout(()=>{
                block.classList.remove('inputWarning')
            }, 2000)

            setOneKey('')
            setTwoKey('')
        }

    }
    const deShifrator = () => {
        if (!deOneKey){
            const block = document.getElementById('deKey_1')
            block.classList.add('inputWarning')

            setTimeout(()=>{
                block.classList.remove('inputWarning')
            }, 2000)
        }
        if (!deTwoKey){
            const block = document.getElementById('deKey_2')
            block.classList.add('inputWarning')

            setTimeout(()=>{
                block.classList.remove('inputWarning')
            }, 2000)
        }
        const message = decodedCode(deOneKey, deTwoKey)
        setDeMessage(message)
    }
    
    const changeEntryShifr = (id) => {
        const blocks = document.querySelectorAll('.btnCodeChange')
        blocks.forEach(element => {
            element.classList.remove('btnCodeChange--active')
        });

        const block = document.getElementById(`btnCode_${id}`)
        block.classList.add('btnCodeChange--active')
    }

    const copyKey = (id) => {
        const key = document.getElementById(`key_${id}`)
        navigator.clipboard.writeText(key.value)
    }
    return (
        <section className='appShifrator'>

            {/* <div className='appDescription subtitle-premedium'>
                     :     
                .       
                 -  .         .
                        ,  . 
            </div> */}

            <div className='appShifratorBlock'>

                <div className='codeBlock'>

                    <div className='btnsCode'>
                        <button
                            onClick={() => {setIsShifr(true); changeEntryShifr(1)}}
                            className='btnCodeChange btnCodeChange--active'
                            id={`btnCode_1`}
                        >
                            
                        </button>
                        <button
                            onClick={() => {setIsShifr(false); changeEntryShifr(2)}}
                            className='btnCodeChange'
                            id={`btnCode_2`}
                        >
                            
                        </button>
                    </div>

                    <div className='entryCode'>
                        

                        { isShifr ?


                        <div className='entryBlockCode'>
                            <input 
                                type='text'
                                placeholder=' ...'
                                className='inputMessageCode'
                                value={message}
                                onChange={e => setMessage(e.target.value)}
                                id='id_message'
                            />
                            <span>  </span>
                           
                            <input 
                                type='range'
                                min='2'
                                max='9'
                                step="1"
                                value={hard}
                                onChange={e => setHard(e.target.value)}
                                className='inputHardCode'
                            />
                            <h3>{hard}</h3>

                            <button
                                className='buttonEntryCode'
                                onClick={createShifr}
                            >
                             
                            </button>
                        </div>

                        :

                        <div className='entryBlockCode'>
                            <input 
                                type='text'
                                placeholder='  '
                                className='inputMessageCode'
                                value={deOneKey}
                                onChange={e => setDeOneKey(e.target.value)}
                                id='deKey_1'
                            />
                            <input 
                                type='text'
                                placeholder='  '
                                className='inputMessageCode'
                                value={deTwoKey}
                                onChange={e => setDeTwoKey(e.target.value)}
                                id='deKey_2'
                            />

                            <button
                                className='buttonEntryCode'
                                onClick={deShifrator}
                            >
                             
                            </button>
                        </div>

                        }
                    </div>

                    <div className='loadingShifr none'>
                        <img src={spinner} className='imgLoadingShifr'/> 
                    </div>

                    <div className='exitCode'>
                        
                        { isShifr ?
                        <div className='exitBlockCode' id='exitCode-1'>
                            
                            <div className='partExitCode'>
                                <span> :</span>
                                <div className='exitKey'>
                                    <input 
                                        className='exitKeyOne'
                                        disabled
                                        value={oneKey}
                                        id='key_1'
                                    />
                                    <svg id="Layer_1" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1" className='svgCopy' onClick={() => copyKey(1)}><path d="m13 20a5.006 5.006 0 0 0 5-5v-8.757a3.972 3.972 0 0 0 -1.172-2.829l-2.242-2.242a3.972 3.972 0 0 0 -2.829-1.172h-4.757a5.006 5.006 0 0 0 -5 5v10a5.006 5.006 0 0 0 5 5zm-9-5v-10a3 3 0 0 1 3-3s4.919.014 5 .024v1.976a2 2 0 0 0 2 2h1.976c.01.081.024 9 .024 9a3 3 0 0 1 -3 3h-6a3 3 0 0 1 -3-3zm18-7v11a5.006 5.006 0 0 1 -5 5h-9a1 1 0 0 1 0-2h9a3 3 0 0 0 3-3v-11a1 1 0 0 1 2 0z"/></svg>
                                </div>
                               
                            </div>    

                            <div className='partExitCode'>
                                <span> :</span>
                                <div className='exitKey' >
                                    <input
                                        className='exitKeyTwo'
                                        disabled
                                        value={twoKey}
                                        id='key_2'
                                    >
                                    </input>
                                    <svg id="Layer_1" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1" className='svgCopy' onClick={() => copyKey(2)}><path d="m13 20a5.006 5.006 0 0 0 5-5v-8.757a3.972 3.972 0 0 0 -1.172-2.829l-2.242-2.242a3.972 3.972 0 0 0 -2.829-1.172h-4.757a5.006 5.006 0 0 0 -5 5v10a5.006 5.006 0 0 0 5 5zm-9-5v-10a3 3 0 0 1 3-3s4.919.014 5 .024v1.976a2 2 0 0 0 2 2h1.976c.01.081.024 9 .024 9a3 3 0 0 1 -3 3h-6a3 3 0 0 1 -3-3zm18-7v11a5.006 5.006 0 0 1 -5 5h-9a1 1 0 0 1 0-2h9a3 3 0 0 0 3-3v-11a1 1 0 0 1 2 0z"/></svg>
                                </div>
                                
                            </div>    
                        </div>

                        :

                        <div className='exitBlockCode' id='exitCode-2'>
                                <span> :</span>
                                <textarea 
                                    className='deMessage'
                                    disabled
                                    value={deMessage}
                                />

                        </div>

                        }
                    </div>

                </div>

            </div>
        </section>
    )
}

export default ShifratorApp;