import React from 'react'

import sendGif from '../interface/img/send.gif'
import send from '../interface/img/svg/send.svg'

const Goto = () => {

    return (
        <React.Fragment>  
            <section className='goto'>
                <div className='container'>
                    <div className='block_goto'>
                        <div className='subtitle-medium'>   . !</div>

                        <div className='flex_goto'>
                            <a href='https://t.me/waytoya' target='on_blank'><div className='send_goto'>
                                <img src={send} className='img_goto' />
                                <span>  </span>
                            </div></a>

                            <img src={sendGif} className='gif_goto' />
                        </div>


                    </div>
                </div>
            </section>
        </React.Fragment>
    )
}

export default Goto;


