import React from 'react';

const Header = () => {

    setTimeout(()=> {
        const block = document.body.querySelector('.header_block')
        block.classList.add('header_block-view')
        
    }, 500 )

    setTimeout(()=> {
        const block = document.body.querySelector('.header_block .subtitle-medium')
        block.classList.add('subtitle-medium-view')
        
    }, 2000)

    return (
        <section className='header'>
            <div className='container'>
                <div className='header_block'>
                    <div className='title-large'>
                         
                    </div>
                    <div className='subtitle-medium'>
                          waytoya   
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Header;