import React from 'react'

import projectsImg1 from '../interface/img/project1.png'
import projectsImg2 from '../interface/img/project2.jpg'
import projectsImg3 from '../interface/img/project3.png'
import projectsImg4 from '../interface/img/project4.jpg'

const AppProjects = () => {
    
    const changeInfo = () => {

        const allProjects = document.body.querySelectorAll('.oneProject')

        allProjects.forEach(project => {
            
            let img = project.querySelector('.img_oneProject')
            let info = project.querySelector('.info_oneProject')

            const coorProject = project.getBoundingClientRect()
            const coorImg = img.getBoundingClientRect()
            const coorInfo = info.getBoundingClientRect()




            //      ,  
            if (coorProject.top <= 0 && coorInfo.bottom > window.innerHeight){
                img.style.position = 'fixed';
                img.style.top = '0'
            } 
            //      -    
            else {
                img.style.position = 'absolute';
            }
            
            if(coorInfo.bottom < coorImg.bottom &&  img.style.top =='0px'){
                img.style.position = 'absolute'
                //     -   (-)  
                img.style.top = info.clientHeight - window.innerHeight + 'px'
            }
            });

    }

    window.addEventListener('scroll', changeInfo)

    return (
        <React.Fragment>
            <section className='appProjects'>
                <div className='container'>
                    <div className='title-medium'>
                    ./3  
                    </div>

                    <div className='subtitle-medium'>
                           .    ,   ,     ,   .
                    </div>
                </div>

                <div className='projects'>

                    <div className='oneProject' id='project_1'>

                        <div className='img_oneProject'>

                            <img src={projectsImg1} className='img_img_oneProject'/>

                        </div>

                        <div className='info_oneProject'>
                            
                            <div className='text_oneProject'>
                                <div className='subtitle_oneProject'>- </div>
                                <div className='title_oneProject'></div>
                            </div>
                            <div className='text_oneProject'>
                                <div className='subtitle-large'> ,     :</div>
                                <div className='skills_text_oneProject'>
                                    <li>HTML</li>
                                    <li>CSS</li>
                                    <li>JavaScript</li>
                                    <li>React</li>
                                    <li>Scss</li>
                                </div>

                                <svg className='plus_oneProject' id="Layer_1" height="512" viewBox="0 0 24 24" width="512" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>

                                <div className='skills_text_oneProject'>
                                    <li>Express</li>
                                    <li>Mobx</li>
                                    <li>React-router-dom</li>
                                    <li>Sequelize</li>
                                    <li>MySQL</li>
                                    <li>Axios</li>
                                    <li>Figma</li>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div className='oneProject' id='project_2'>

                        <div className='img_oneProject'>

                            <img src={projectsImg2} className='img_img_oneProject'/>

                        </div>

                        <div className='info_oneProject'>
                            
                            <div className='text_oneProject'>
                                <div className='subtitle_oneProject'>-  </div>
                                <div className='title_oneProject'>PORTEN</div>
                            </div>
                            <div className='text_oneProject'>
                                <div className='subtitle-large'> ,     :</div>
                                <div className='skills_text_oneProject'>
                                    <li>HTML</li>
                                    <li>CSS</li>
                                </div>

                                <svg className='plus_oneProject' id="Layer_1" height="512" viewBox="0 0 24 24" width="512" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>

                                <div className='skills_text_oneProject'>
                                    <li>Figma</li>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div className='oneProject' id='project_3'>

                        <div className='img_oneProject'>

                            <img src={projectsImg3} className='img_img_oneProject'/>

                        </div>

                        <div className='info_oneProject'>
                            
                            <div className='text_oneProject'>
                                <div className='subtitle_oneProject'> -    </div>
                                <div className='title_oneProject'>MEDI</div>
                            </div>
                            <div className='text_oneProject'>
                                <div className='subtitle-large'> ,     :</div>
                                <div className='skills_text_oneProject'>
                                    <li>HTML</li>
                                    <li>CSS</li>
                                    <li>JavaScript</li>
                                </div>

                                <svg className='plus_oneProject' id="Layer_1" height="512" viewBox="0 0 24 24" width="512" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>

                                <div className='skills_text_oneProject'>
                                    <li>Figma</li>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div className='oneProject' id='project_4'>

                        <div className='img_oneProject'>

                            <img src={projectsImg4} className='img_img_oneProject'/>

                        </div>

                        <div className='info_oneProject'>
                            
                            <div className='text_oneProject'>
                                <div className='subtitle_oneProject'> </div>
                                <div className='title_oneProject'>Edururu</div>
                            </div>
                            <div className='text_oneProject'>
                                <div className='subtitle-large'> ,     :</div>
                                <div className='skills_text_oneProject'>
                                    <li>HTML</li>
                                    <li>CSS</li>
                                    <li>JavaScript</li>
                                </div>

                                <svg className='plus_oneProject' id="Layer_1" height="512" viewBox="0 0 24 24" width="512" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>

                                <div className='skills_text_oneProject'>
                                    <li>Figma</li>
                                    <li>Python</li>
                                    <li>Django</li>
                                    <li>MySQL</li>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </section>
            
        </React.Fragment>
        
    )
}

export default AppProjects