import React, {  useState, useContext, useEffect } from 'react';
import {Context} from '../../index'
import NoteOfNotebook from './NoteOfNotebook';

import TimeApp from './TimeApp';

const NotebookApp = () => {

    const {back, notebook} = useContext(Context)

    const [activatedNote, setActivatedNote] = useState(false)
    const [idNote, setIdNote] = useState(0)

    const openNote = (id) => {
        setIdNote(id)
        setActivatedNote(true)
    }

    useEffect(()=>{
        setIdNote(0)
    },[activatedNote])

    return (
        <React.Fragment>
            <div className='notebookPhone'>

                <TimeApp />

                <div className='subtitle-medium'>
                    
                </div>

                {activatedNote
                ?
                <React.Fragment>

                <NoteOfNotebook 
                    setActivatedNote={setActivatedNote}
                    id={idNote}
                />

                </React.Fragment>

                :

                <React.Fragment>
                <div className='subtitle-premedium'>
                     :
                </div>
                <div className='list_notebook'>
                    
                    {notebook.notes.map(note => 
                         <div className='oneNote' onClick={() => openNote(note.number)} key={note.number}>
                            <h3>{note.number}</h3>
                            <div className='name_oneNote'>
                                {note.title}
                            </div>
                        </div>
                    )}
                   
                    
                </div>
                <svg className='add_notebook' onClick={() => setActivatedNote(true)} id="Layer_1" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>
                </React.Fragment>
                }       
            </div>

            <div className='back' onClick={() => back.setNotebook(false)}></div>

        </React.Fragment>
    )
}

export default NotebookApp;