const createAlert = (name) => {
    if (!document.getElementById('alertBlock')){
        let alertBlock = document.createElement('div');
        alertBlock.setAttribute('id', 'alertBlock')
        alertBlock.innerHTML = ''
        
        let phone = document.body.querySelector(`.${name}`)
        phone.append(alertBlock)
    }
    let alertBlock = document.getElementById('alertBlock');

    // alertBlock.style.display = 'block'
    alertBlock.classList.add('alertBlock--active')
    setTimeout(()=>{
        // alertBlock.style.display = 'none'
        alertBlock.classList.remove('alertBlock--active')

    }, 1500)
}

export {
    createAlert
}