import React from 'react'

import scss from '../interface/img/svg/sass.svg'
import SkillsMore from './SkillsMore'

const Skills = () => {

    const isVisible = (element) => {

        const coords = element.getBoundingClientRect()

        return coords.top < window.innerHeight - 100
    }

    
    const isVisible_2B = (element) => {

        const coords = element.getBoundingClientRect()

        return coords.top < window.innerHeight - 400
    }

    const isVisible_4B = (element) => {

        const coords = element.getBoundingClientRect()

        return coords.top < window.innerHeight - 500
    }

    const isVisible_5B = (element) => {

        const coords = element.getBoundingClientRect()

        return coords.top < 200
    }

    const showSuccess = () => {
        for (let svg of document.body.querySelectorAll('.success_skill')){
            if (isVisible(svg)){
                svg.classList.add('success_skill--active')
                
                setTimeout(()=>{
                    svg.classList.add('success_skill--active--svg')
                }, 400)

            } else {
                svg.classList.remove('success_skill--active')
                setTimeout(()=>{
                    svg.classList.remove('success_skill--active--svg')
                }, 400)
            }

            //    
            //   
            if (isVisible_2B(svg)){
                //  
                document.body.querySelector('.lineHor_1sm--250w').classList.add('lineHor_1sm--250w--active')

                //  
                setTimeout(()=>{
                    document.body.querySelector('.lineVert_1sm--200h').classList.add('lineVert_1sm--200h--active')

                    //   
                    setTimeout(()=>{
                        document.body.querySelector('.lineHor_1sm--50w').classList.add('lineHor_1sm--50w--active')
                   
                        //   
                        setTimeout(()=>{
                            document.getElementById('1sm_skill').classList.add('opacityShow')
                        }, 300)
                        
                    }, 300)

                }, 300)

            } 
            if (isVisible_4B(svg)){
                //  
                document.body.querySelector('.lineHor_2sm--150w').classList.add('lineHor_2sm--150w--active')

                //  
                setTimeout(()=>{
                    document.body.querySelector('.lineVert_2sm--400h').classList.add('lineVert_2sm--400h--active')

                    //   
                    setTimeout(()=>{
                        document.body.querySelector('.lineHor_2sm--100w').classList.add('lineHor_2sm--100w--active')
                   
                        //   
                        setTimeout(()=>{
                            document.getElementById('2sm_skill').classList.add('opacityShow')
                        }, 300)
                        
                    }, 300)

                }, 300)

            } 
            if (isVisible_5B(svg)){
                setTimeout(()=>{
                    document.getElementById('3sm_skill').classList.add('opacityShow')
                }, 400)

                setTimeout(()=>{
                    document.getElementById('4sm_skill').classList.add('opacityShow')
                }, 800)
            } 
                
        }
        
    }

    window.addEventListener('scroll', showSuccess)

    return (
        <React.Fragment>
           <section className='skills_section'>
            <div className='container'>

                <div className='title-medium' >
                        
                </div>

                <div className='skills_section_block'>

                    <div className='skills'>
                        <div className='name_blockSkill1'>
                        
                        </div>

                        <div className='oneSkill'>
                        
                            <div className='point_skill'></div>
                            
                            <svg className='logo_skill' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                <path fill="#E34F26" d="M71,460 L30,0 481,0 440,460 255,512"/>
                                <path fill="#EF652A" d="M256,472 L405,431 440,37 256,37"/>
                                <path fill="#EBEBEB" d="M256,208 L181,208 176,150 256,150 256,94 255,94 114,94 115,109 129,265 256,265zM256,355 L255,355 192,338 188,293 158,293 132,293 139,382 255,414 256,414z"/>
                                <path fill="#FFF" d="M255,208 L255,265 325,265 318,338 255,355 255,414 371,382 372,372 385,223 387,208 371,208zM255,94 L255,129 255,150 255,150 392,150 392,150 392,150 393,138 396,109 397,94z"/>
                            </svg>

                            <div className='name_skill'>
                                HTML
                            </div>

                            <div className='longer_skill'></div>

                            <div className='success_skill '>

                            </div>

                        </div>

                        <div className='oneSkill'>
                        
                            <div className='point_skill'></div>

                            <svg className='logo_skill'  xmlns="http://www.w3.org/2000/svg" width="512" height="512" viewBox="0 0 512 512">
                                <path fill="#264de4" d="M71.357 460.819L30.272 0h451.456l-41.129 460.746L255.724 512z"/>
                                <path fill="#2965f1" d="M405.388 431.408l35.148-393.73H256v435.146z"/>
                                <path fill="#ebebeb" d="M124.46 208.59l5.065 56.517H256V208.59zM119.419 150.715H256V94.197H114.281zM256 355.372l-.248.066-62.944-16.996-4.023-45.076h-56.736l7.919 88.741 115.772 32.14.26-.073z"/>
                                <path fill="#fff" d="M255.805 208.59v56.517H325.4l-6.56 73.299-63.035 17.013v58.8l115.864-32.112.85-9.549 13.28-148.792 1.38-15.176 10.203-114.393H255.805v56.518h79.639L330.3 208.59z"/>
                            </svg>

                            <div className='name_skill'>
                                CSS
                            </div>

                            <div className='longer_skill'></div>

                            <div className='success_skill'>

                            </div>

                        </div>

                        <div className='oneSkill'>
                        
                            <div className='point_skill'></div>

                            <svg className='logo_skill js_skill' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 128 128"><path d="M1.408 1.408h125.184v125.185h-125.184z"/><path fill="#323330" d="M116.347 96.736c-.917-5.711-4.641-10.508-15.672-14.981-3.832-1.761-8.104-3.022-9.377-5.926-.452-1.69-.512-2.642-.226-3.665.821-3.32 4.784-4.355 7.925-3.403 2.023.678 3.938 2.237 5.093 4.724 5.402-3.498 5.391-3.475 9.163-5.879-1.381-2.141-2.118-3.129-3.022-4.045-3.249-3.629-7.676-5.498-14.756-5.355l-3.688.477c-3.534.893-6.902 2.748-8.877 5.235-5.926 6.724-4.236 18.492 2.975 23.335 7.104 5.332 17.54 6.545 18.873 11.531 1.297 6.104-4.486 8.08-10.234 7.378-4.236-.881-6.592-3.034-9.139-6.949-4.688 2.713-4.688 2.713-9.508 5.485 1.143 2.499 2.344 3.63 4.26 5.795 9.068 9.198 31.76 8.746 35.83-5.176.165-.478 1.261-3.666.38-8.581zm-46.885-37.793h-11.709l-.048 30.272c0 6.438.333 12.34-.714 14.149-1.713 3.558-6.152 3.117-8.175 2.427-2.059-1.012-3.106-2.451-4.319-4.485-.333-.584-.583-1.036-.667-1.071l-9.52 5.83c1.583 3.249 3.915 6.069 6.902 7.901 4.462 2.678 10.459 3.499 16.731 2.059 4.082-1.189 7.604-3.652 9.448-7.401 2.666-4.915 2.094-10.864 2.07-17.444.06-10.735.001-21.468.001-32.237z"/></svg>

                            <div className='name_skill'>
                                JavaScript
                            </div>

                            <div className='longer_skill'></div>

                            <div className='success_skill'>

                            </div>

                        </div>

                        <div className='oneSkill'>
                        
                            <div className='point_skill'></div>

                            <svg className='logo_skill' xmlns="http://www.w3.org/2000/svg" viewBox="-11.5 -10.23174 23 20.46348">
                                <circle cx="0" cy="0" r="2.05" fill="#61dafb"/>
                                <g stroke="#61dafb" strokeWidth="1" fill="none">
                                    <ellipse rx="11" ry="4.2"/>
                                    <ellipse rx="11" ry="4.2" transform="rotate(60)"/>
                                    <ellipse rx="11" ry="4.2" transform="rotate(120)"/>
                                </g>
                            </svg>

                            <div className='name_skill'>
                                React
                            </div>

                            <div className='longer_skill'></div>

                            <div className='success_skill'>

                            </div>

                        </div>

                        <div className='oneSkill'>
                        
                            <div className='point_skill'></div>

                            <img src={scss} className='logo_skill' />

                            <div className='name_skill'>
                                &nbsp;(scss)
                            </div>

                            <div className='longer_skill'></div>

                            <div className='success_skill'>

                            </div>

                        </div>

                    </div>

                    <SkillsMore />
                </div>
            </div>
        </section>

        <section className='banner_MyWorks'>
            <div className='line_MyWorks line_left_MyWorks'></div>
            <div className='title-medium'>
                &nbsp;
            </div>
            <div className='line_MyWorks'></div>
        </section>

        </React.Fragment>
        
    )
}

export default Skills