import { observer } from 'mobx-react-lite';
import React, {  useState } from 'react';

import batteryIcon from '../../interface/img/png/battery.png'

const TimeApp = observer(() => {

    const timer = () => {
        let times = new Date();
        let hours = times.getHours()
        let minutes = times.getMinutes()
        if (hours < 10){
            hours = '0' + hours
        }
        if (minutes < 10){
            minutes = '0' + minutes
        }
        
        if (document.body.querySelector('.phoneTimeAll')){
            document.body.querySelector('.phoneTimeAll').innerHTML = hours + ':' + minutes
        }

        }

    setInterval(() => {
        timer()
      }, 1000);
    
    
    return (
        <React.Fragment>
            
            <div className='phoneTimeAll'>9:41</div>
            <img src={batteryIcon} className='phoneBatteryAll'/>
        </React.Fragment>
    )
})

export default TimeApp;