import React, { useContext } from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import BackPhone from './store/BackPhoneStore'
import NotebookPhone from './store/NotebookPhoneStore'

const root = ReactDOM.createRoot(document.getElementById('root'));

export const Context = React.createContext(null)

root.render(
  <Context.Provider
    value={{
      back: new BackPhone(),
      notebook: new NotebookPhone(),
    }}
  >
    <React.StrictMode>
      <App />
    </React.StrictMode>
  </Context.Provider>

);
