import { makeAutoObservable } from "mobx";

class BackPhone {

    constructor(){
        this._settings = false
        this._notebook = false
        this._calculator = false
        this._clock = false
        makeAutoObservable(this)
    }

    setSettings(bool){
        this._settings = bool
    }
    setNotebook(bool){
        this._notebook = bool
    }
    setCalculator(bool){
        this._calculator = bool
    }
    setClock(bool){
        this._clock = bool
    }
   
    get settings(){
        return this._settings
    }
    get notebook(){
        return this._notebook
    }
    get calculator(){
        return this._calculator
    }
    get clock(){
        return this._clock
    }

}

export default BackPhone;