import { makeAutoObservable } from "mobx";

class NotebookPhone {

    constructor(){
        this._notes = [
            {
                'number': '1',
                'title': '  !',
                'text': '     ...'
            },
            {
                'number': '2',
                'title': ' - ',
                'text': ''
            },
        ]
        makeAutoObservable(this)
    }

    setNotes(notes){
        this._notes = notes
    }
    
    get notes(){
        return this._notes
    }

}

export default NotebookPhone;