import { observer } from 'mobx-react-lite';
import React, { useState } from 'react';

import apple from './../interface/img/svg/apple-white.svg'
import Phone from './Phone';

const AppPhone = observer(() => {

    const [activated, setActivated] = useState(false)

    const start = () => {
        const button = document.body.querySelector('.buttonShowPhone')
        button.classList.add('buttonShowPhone--inactive')

        //   
        setTimeout(()=>{
            button.style.display = 'none';

        }, 300)

        //   
        setTimeout(()=>{
            const showPhone = document.body.querySelector('.showPhone')
            showPhone.classList.add('showPhone--loading')

            const imgLoadingPhone = document.body.querySelector('.appleLogo')
            imgLoadingPhone.classList.add('appleLogo--active')

            setTimeout(()=>{
                imgLoadingPhone.style.opacity = 1;
            }, 500)

        }, 400)


        let mobileCursor = document.createElement('div');
        mobileCursor.setAttribute('id', 'mobileCursor');

        //    
        mobileCursor.addEventListener('mousedown', () => {
            mobileCursor.style.width = '23px';
            mobileCursor.style.height = '23px';
            mobileCursor.style.opacity = 1;

        })
        mobileCursor.addEventListener('mouseup', () => {
            mobileCursor.style.width = '26px';
            mobileCursor.style.height = '26px';
            mobileCursor.style.opacity = 0.7;

        })

        //   
        let phone = document.body.querySelector('.phone')
        phone.append(mobileCursor)
        phone.style.cursor = 'none';

        //   
        setTimeout(()=>{
            const showPhone = document.body.querySelector('.showPhone')
            showPhone.style.display = 'none';

            setActivated(true)
        }, 5000)
    }

    const mouseMove = (event) => {
        if (activated == true){
            let mobileCursor = document.getElementById('mobileCursor')
            mobileCursor.style.display = 'block';
    
            let phone = document.body.querySelector('.phone')
            
            var coors = phone.getBoundingClientRect();
            var x = event.clientX - coors.left;
            var y = event.clientY - coors.top;
    
            mobileCursor.style.left = x - 13 + 'px'  ;
            mobileCursor.style.top = y - 13 + 'px' ;
    
        }
    }
    const mouseOut = (event) => {
        if (activated == true){
            let divBlock = document.getElementById('mobileCursor')
            divBlock.style.display = 'none';
        }
    }

    const viewPhone = () => {
        const block = document.body.querySelector('.appPhone')

        const coords = block.getBoundingClientRect()

        if(coords.top < window.innerHeight - 200){
            block.classList.add('opacityShow')
        }
    }

    window.addEventListener('scroll', viewPhone)

    return (
        <section className='appPhone'>
            <div className='container'>

                <div className='title-medium'>
                ./1  
                </div>

                <div className='subtitle-medium'>
                       .       .   ,       ,     ,      .      ,       . ,    -      .
                </div>
                
                <div className='appPhone_block'>



                    
                    <div className='phone' onMouseMove={(e)=> mouseMove(e)} onMouseOut={(e) => mouseOut(e)}>
                            
                        {activated &&

                        <Phone />

                        }

                        <div className='showPhone'>
                            <button
                                onClick={start}
                                className='buttonShowPhone'
                            >
                            
                            </button>

                            <img src={apple} className='appleLogo' />
                        </div>

                    </div>

                </div>
            </div>
        </section>
    )
})

export default AppPhone;