import React, {  useState, useContext, useEffect } from 'react';
import {Context} from '../../index'


const NoteOfNotebook = ({id, setActivatedNote}) => {

    const {notebook} = useContext(Context)

    const [title, setTitle] = useState(id == 0 ? '' : notebook.notes[id - 1].title)
    const [text, setText] = useState(id == 0 ? '' : notebook.notes[id - 1].text)
    const [idIs, setIdIs] = useState(id)

    const createNote = () => {
        const length = notebook.notes.length + 1
        notebook.setNotes([...notebook.notes, {'number': length, 'title': title, 'text': text}])
        setActivatedNote(false)
    }
    const changeNote = () => {
        notebook.setNotes(notebook.notes.map(note => note.number == idIs + '' ? {'number': idIs, 'title': title, 'text': text} :  note))
        setActivatedNote(false)
    }

    return (
        <React.Fragment>
                <div className='create_notebook'>
                    <input 
                        className='title_create_notebook'
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                    >
                    </input>
                    <textarea 
                        className='text_create_notebook'
                        value={text}
                        onChange={(e) => setText(e.target.value)}
                    >
                    </textarea>
                </div>

                {idIs == 0 
                ? 
                <svg className='create_btn_notebook' onClick={() => createNote()} id="Layer_1" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" data-name="Layer 1"><path d="m12 0a12 12 0 1 0 12 12 12.013 12.013 0 0 0 -12-12zm4 13h-3v3a1 1 0 0 1 -2 0v-3h-3a1 1 0 0 1 0-2h3v-3a1 1 0 0 1 2 0v3h3a1 1 0 0 1 0 2z"/></svg>
                :
                <svg className='edit_btn_notebook' onClick={() => changeNote()} xmlns="http://www.w3.org/2000/svg" version="1.1" width="512" height="512" x="0" y="0" viewBox="0 0 24 24" ><g><path xmlns="http://www.w3.org/2000/svg" d="M1.172,19.119A4,4,0,0,0,0,21.947V24H2.053a4,4,0,0,0,2.828-1.172L18.224,9.485,14.515,5.776Z" fill="#ffffff" data-original="#000000"/><path xmlns="http://www.w3.org/2000/svg" d="M23.145.855a2.622,2.622,0,0,0-3.71,0L15.929,4.362l3.709,3.709,3.507-3.506A2.622,2.622,0,0,0,23.145.855Z" fill="#ffffff" data-original="#000000"/></g></svg>
                }

                <svg className='clear_btn_notebook' onClick={() => setActivatedNote(false)} xmlns="http://www.w3.org/2000/svg" id="Outline" viewBox="0 0 24 24" ><path d="M16,8a1,1,0,0,0-1.414,0L12,10.586,9.414,8A1,1,0,0,0,8,9.414L10.586,12,8,14.586A1,1,0,0,0,9.414,16L12,13.414,14.586,16A1,1,0,0,0,16,14.586L13.414,12,16,9.414A1,1,0,0,0,16,8Z"/><path d="M12,0A12,12,0,1,0,24,12,12.013,12.013,0,0,0,12,0Zm0,22A10,10,0,1,1,22,12,10.011,10.011,0,0,1,12,22Z"/></svg>



        </React.Fragment>
    )
}

export default NoteOfNotebook;