import React, {  useState, useContext } from 'react';
import {Context} from '../../index'


import icon1 from '../../interface/img/svg/iSet-1.svg'
import icon2 from '../../interface/img/svg/iSet-2.svg'
import icon3 from '../../interface/img/svg/iSet-3.svg'
import icon4 from '../../interface/img/svg/iSet-4.svg'
import icon5 from '../../interface/img/svg/iSet-5.svg'
import icon6 from '../../interface/img/svg/iSet-6.svg'
import icon7 from '../../interface/img/svg/iSet-7.svg'
import icon8 from '../../interface/img/svg/iSet-8.svg'
import icon9 from '../../interface/img/svg/iSet-9.svg'
import icon10 from '../../interface/img/svg/iSet-10.svg'
import icon11 from '../../interface/img/svg/iSet-11.svg'
import icon12 from '../../interface/img/svg/iSet-12.svg'
import icon13 from '../../interface/img/svg/iSet-13.svg'
import icon14 from '../../interface/img/svg/iSet-14.svg'
import icon15 from '../../interface/img/svg/iSet-15.svg'
import icon16 from '../../interface/img/svg/iSet-16.svg'
import icon17 from '../../interface/img/svg/iSet-17.svg'
import TimeApp from './TimeApp';

import { createAlert } from '../../app/alertPhone';

const SettingsApp = () => {

    const {back} = useContext(Context)

    setTimeout(()=> {
        let blockSettings = document.body.querySelectorAll('.oneSetting')

        blockSettings.forEach(block => {
            block.addEventListener('click', () => {
                createAlert('settingsPhone')
            })
        });
        
    }, 1000)

    return (
        <React.Fragment>
            <div className='settingsPhone' id='settingsPhone'>

                <TimeApp />


                <div className='subtitle-medium'>
                    
                </div>
                <div className='listSettings'>
                    <div className='oneSetting'>
                        <img src={icon1} className='icon_oneSetting background-blue1' />
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon2} className='icon_oneSetting background-green1'/>
                        <span>Bluetooth</span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon3} className='icon_oneSetting background-red2'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon4} className='icon_oneSetting background-blue2'/>
                        <span></span>
                    </div>

                    <div className='oneSetting oneSetting--empty'></div>

                    <div className='oneSetting'>
                        <img src={icon5} className='icon_oneSetting background-green3'/>
                        <span>FaceID</span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon6} className='icon_oneSetting background-red1'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon7} className='icon_oneSetting background-red3'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon8} className='icon_oneSetting background-pink1'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon9} className='icon_oneSetting background-orange3'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon10} className='icon_oneSetting background-blue3'/>
                        <span>TouchID</span>
                    </div>

                    <div className='oneSetting oneSetting--empty'></div>


                    <div className='oneSetting'>
                        <img src={icon11} className='icon_oneSetting background-green3'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon12} className='icon_oneSetting background-blue1'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon13} className='icon_oneSetting background-red1'/>
                        <span>  </span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon14} className='icon_oneSetting background-green1'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon15} className='icon_oneSetting background-pink2'/>
                        <span></span>
                    </div>
                    <div className='oneSetting'>
                        <img src={icon16} className='icon_oneSetting background-blue2'/>
                        <span></span>
                    </div>

                    <div className='oneSetting oneSetting--empty'></div>

                    <div className='oneSetting'>
                        <img src={icon17} className='icon_oneSetting background-blue1'/>
                        <span> </span>
                    </div>
                </div>
            </div>

            <div className='back' onClick={() => {back.setSettings(false)}}></div>

        </React.Fragment>
    )
}

export default SettingsApp;