import React, {  useContext, useState} from 'react';
import { Context } from '../../index';
import TimeApp from './TimeApp';
import { observer } from 'mobx-react-lite';

import batteryIcon from '../../interface/img/png/battery.png'

const ClockApp = observer(() => {
    const {back} = useContext(Context)

    const timer = () => {
        let times = new Date();
        let hours = times.getHours()
        let minutes = times.getMinutes()
        let seconds = times.getSeconds()
        if (hours < 10){
            hours = '0' + hours
        }
        if (minutes < 10){
            minutes = '0' + minutes
        }
        if (seconds < 10){
            seconds = '0' + seconds
        }
        if (document.body.querySelector('.input_clockPhone')){
            document.body.querySelector('.input_clockPhone').innerHTML = hours + ':' + minutes + ':' + seconds
        }
        }
    
    setInterval(() => {
        timer()
    }, 1000);


    return (
        <React.Fragment>
            <div className='clockPhone'>

                <TimeApp />
                
                <div className='subtitle-medium'>
                    
                </div>

                <div className='block_clockPhone'>
                    <div  
                        className='input_clockPhone'
                    >
                    </div>

                </div>
            </div>

            <div className='back' onClick={() => back.setClock(false)}></div>

        </React.Fragment>
    )
})

export default ClockApp;